import React, {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import {Button} from "@material-ui/core";
 import ThemeContext from "../../context";

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TransitionsModal({lastMessage}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const {lang} = useContext(ThemeContext);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button  variant="outlined" type="button" onClick={handleOpen}>
      {lang==="ru" ? `Покажи мое последние сообщение` : 'Show me lats message'}
      </Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <h2 id="transition-modal-title">Your message</h2>

            <p id="transition-modal-description">{lang==="ru" ? `имя` : 'name'}: {lastMessage.name}</p>
              <p id="transition-modal-description">email: {lastMessage.email}</p>
             <p id="transition-modal-description">{lang==="ru" ? `сообщение` : 'message'}: {lastMessage.message}</p>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
