import {useForm} from "react-hook-form";
import * as yup from 'yup';
import {yupResolver} from "@hookform/resolvers/yup";
import {Button, TextField} from "@material-ui/core";
import emailJs from "emailjs-com";
import React, {useContext, useEffect, useState} from "react";
import "./contact.css"
import TimeForText from "./timerText";
import ThemeContext from "../../context";
import TransitionsModal from "./modalMessage";




const Contact = () =>{
    const {lang} = useContext(ThemeContext);
    const [correctForm,setCorrectForm] = useState(undefined);
    const [timerShow,setTimerShow] = useState(undefined);

    const [lastMessage,setLastMessage] = useState(undefined);
    const [showBtn,setShowBtn] = useState(false);



     const schema = yup.object().shape({
        name: yup
            .string()
            .matches(/^([^0-9 ]*)$/,lang==="ru" ? `Имя не должно иметь пробелов и цифр` : 'Name must be without numbers and spaces')
            .required(lang==="ru" ? `Имя должно быть заполнено` : 'Name is a required field')
            .min(2, lang==="ru" ? `Слишком коротко` : 'Too short')
            .max(50, lang==="ru" ? `Слишком много` : 'Too long'),
        email: yup
            .string()
            .email(lang==="ru" ? `Email должен быть правильного формата` : 'Email should have correct format')
            .required(lang==="ru" ? `Email должен быть заполнен` : 'Email is a required field')
            .min(2, lang==="ru" ? `Слишком коротко` : 'Too short')
            .max(50, lang==="ru" ? `Слишком много` : 'Too long'),
        message: yup
            .string()
            .required(lang==="ru" ? `Сообщение должно быть заполнено` : 'Message is a required field')
            .min(15, lang==="ru" ? `Слишком коротко` : 'Too short'),
    });



    const sendEmail = (e) =>{
        emailJs.sendForm('service_8zy77vd', 'template_sv8z71j', e.target,
            'user_xfg6Lq0YEVmdSv9KquWrU')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
                setCorrectForm("Error try again later");
            });
    }

    const { register, handleSubmit,setValue,reset, formState: { errors }} = useForm(
        {
            mode:"onChange",
            resolver: yupResolver(schema)
        }
    );

    const onSubmit = (values,e) => {
        sendEmail(e)
        console.log(values)
        localStorage.setItem('form', JSON.stringify(values))
        setTimerShow(<TimeForText/>)
        reset();

    };


    useEffect(() => {
            const defaultValue = JSON.parse(localStorage.getItem('form'))
            setValue('test', defaultValue?.test)

        if (defaultValue===null){setShowBtn(false)}
        else  {setLastMessage(defaultValue);
         setShowBtn(true)}
        }, [register]
    )



    return (
        <section className="contactWrap">

            <form  className="contact-form" onSubmit={handleSubmit(onSubmit)}>
                <TextField
                    name="name"
                    autoComplete="off"
                    {...register("name")}
                    error={!!errors.name}
                    helperText={errors?.name?.message}
                    placeholder={lang==="ru" ? `Имя` : 'Name'}
                    className="form__item"
                />
                <TextField
                    name="email"
                    autoComplete="off"
                    {...register("email")}
                    placeholder={lang==="ru" ? `Email` : 'Email'}
                    error={!!errors.email}
                    helperText={errors?.email?.message}
                    className="form__item"

                />
                <TextField
                    name="message"
                    autoComplete="off"
                    {...register("message")}
                    placeholder={lang==="ru" ? `Сообщение` : 'Message'}
                    error={!!errors.message}
                    helperText={errors?.message?.message}
                    className="form__item"

                />
                <p className="boxTimer"> {timerShow} </p>
                {correctForm}
                <Button variant="outlined"  value="Send" className="sendMessage" type="submit">
                    {lang==="ru" ? `Отправить` : 'Send'}
                </Button>
    
                {showBtn===true ?
                    (<div className="lastMessage"> 
                    <TransitionsModal lastMessage={lastMessage} />
                    </div>
                    )
                 : null
                }
            </form>
            
        </section>
    );
}
export default Contact;