import React, {useContext, useState} from 'react';
import Switch from '@material-ui/core/Switch';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import ThemeContext from "../../context";
import "./project.css";

export default function FormControlLabelPosition({postData,setRefresh,refresh}) {
    const {lang} = useContext(ThemeContext);
    const [textSort,setTextSort] = useState(null);
    const [changeSort,setChangeSort] = useState(false);
    return (
        <FormControl component="fieldset">
            <FormGroup aria-label="position" row>
                <FormControlLabel
                    value="top"
                    control={<Switch color="primary" />}
                    label={lang==="ru" ? `Сотрировка по дате` : 'Sort by data'}
                    labelPlacement="top"
                    onClick={() => {
                    postData.reverse()
                        refresh === false ? setRefresh(true) : setRefresh(false);
                        changeSort === false ? setChangeSort(true) : setChangeSort(false);
                        changeSort === false ? setTextSort(lang==="ru" ? `Прошлые` : 'Older') : setTextSort(lang==="ru" ? `Новые` : 'New');
                    }}
                />
            </FormGroup>
            <p className="textSort">
                {textSort}
            </p>
        </FormControl>
    );
}
